.meus_filmes {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.meus_filmes h1 {
    margin-bottom: 14px;
}

.meus_filmes span {
    font-size: 18px;
}

.meus_filmes ul {
    width: 720px;
}

.meus_filmes ul li {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 14px;
}

.meus_filmes div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.meus_filmes div a {
    text-decoration: none;
    color: #116feb;
    margin-right: 14px;
}